<template>
  <div>
    <div class="container mt-5 pt-5">
      <div class="dark-font">
    <mdb-breadcrumb class="white">
      <mdb-breadcrumb-item><router-link to="/products" class="teal-text">Servicios</router-link></mdb-breadcrumb-item>
      <mdb-breadcrumb-item><router-link :to="`/products/${product.categorias}`" class="teal-text">{{ product.categorias }}</router-link></mdb-breadcrumb-item>
      <mdb-breadcrumb-item active>{{ product.Nombre }}</mdb-breadcrumb-item>
    </mdb-breadcrumb>
  </div>
      <div class="row mt-3">
        <div class="col-md-7">
          <img
            :src="require(`../assets/11/${product.imagenes}`)"
            class="img-fluid"
            alt=""
          />
        </div>
        <div class="col-md-5">
          <h2 class="mb-4 mt-3">
            <strong>{{ product.Nombre }}</strong>
          </h2>
          <p>
            {{ product.descripcionCorta }}
          </p>
          <div class="text-center">
            <a
              :href="`https://api.whatsapp.com/send?phone=5212293060014&text=Quiero%20hacer%20una%20cotizaci%C3%B3n%20de%20${product.Nombre}%20`"
              ><mdb-btn color="success" size="lg" rounded
                >Cotizar<mdb-icon fab icon="whatsapp" class="ml-2" /></mdb-btn
            ></a>
          </div>
        </div>
      </div>
      <mdb-card class="card-body mt-5 my-red white-text text-white">
        <div v-if="product.descripcion">
          <h3 class="mt-4">Descripción:</h3>
          <hr />
          <mdb-card-text class="mt-4">{{ product.descripcion }}</mdb-card-text>
        </div>
       
        <h3 class="mt-4">Nuestros trabajos:</h3>
        <hr />
        <div class="container mt-4">
          <div class="row">
            <div
              v
              v-for="(item, i) in images"
              class="col-md-4 mb-4 mx-auto"
              :key="i"
            >
              <img
                :src="require(`../assets/cercas/${item}`)"
                class="img-fluid"
              />
            </div>
          </div>
        </div>
      </mdb-card>
      <h4 class="font-weight-bold mt-5 dark-grey-text">
        <strong>Te podrian interesar:</strong>
      </h4>
      <hr class="mb-5" />
      <div class="row mb-2 pb-4">
        <div class="col-md-3" v-for="(item, i) in fourRandom" :key="i">
          <router-link :to="`/product/${item.ID}`">
            <item-card
              :title="item.Nombre"
              :categoria="item.categorias"
              :image="item.imagenes"
              :description="slice(item.descripcionCorta)"
              class="mb-3"
            />
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mdbBtn, mdbCard, mdbCardText, mdbIcon, mdbBreadcrumb, mdbBreadcrumbItem } from "mdbvue";
import ItemCard from "../components/ItemCard";

export default {
  components: {
    mdbBtn,
    ItemCard,
    mdbCard,
    mdbCardText,
    mdbIcon,
    mdbBreadcrumb, mdbBreadcrumbItem
  },
  data() {
    return {
      fourRandom: [],
      id: this.$route.params.id,
      product: {},
      allImages: [
        "1.jpg",
        "2.jpg",
        "3.jpg",
        "4.jpg",
        "5.jpg",
        "6.jpg",
        "7.jpg",
        "8.jpg",
        "9.jpg",
        "10.jpg",
        "11.jpg",
        "12.jpg",
        "13.jpg",
        "16.jpg",
      ],
      images: [],
    };
  },
  props: {
    items: Array,
  },
  mounted() {
    this.getProduct();
    this.randomImages();
    this.randomProducts();
  },
  watch: {
    $route(to) {
      this.id = to.params.id;
      this.getProduct();
      this.randomImages();
      this.randomProducts();
    },
  },
  methods: {
    getProduct() {
      this.items.forEach((item) => {
        if (item.ID == this.id) {
          this.product = item;
        }
      });
    },
    randomImages() {
      let temp = [...this.allImages];
      let images = [];
      for (let i = 0; i < 6; i++) {
        let n = Math.floor(Math.random() * temp.length);
        images.push(temp[n]);
        temp.splice(n, 1);
      }
      this.images = images;
    },
    randomProducts() {
      let temp = [...this.items];
      let products = [];
      for (let i = 0; i < 4; i++) {
        let n = Math.floor(Math.random() * temp.length);
        products.push(temp[n]);
        temp.slice(n, 1);
      }
      this.fourRandom = products;
    },
    slice(text) {
      let sliced = text.slice(0,70) + '...'
      return sliced
    }
  },
};
</script>

